a {
  text-decoration: none; /* Отменяем подчеркивание у ссылки */
  color: currentColor;
}

html {
  box-sizing: border-box;
}
*,
*::before,                /* добавит значение бокса */                                 
*::after {
  box-sizing: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a,
input,
button,
textarea {
  outline: none;
}

button {
  font-family: inherit;
  border: none;
  outline: none;
  cursor: pointer;
}

